.dresses-container {
  display: flex;
  text-align: center; }
  .dresses-container ul {
    margin: 0;
    padding: 0; }
    .dresses-container ul li {
      list-style: none;
      padding: 0 8px;
      display: inline-block; }

.dress-container {
  position: relative;
  margin-top: 56px;
  border-radius: 4px; }

.dress-card {
  position: relative;
  overflow: hidden;
  border-radius: 4px; }
  .dress-card img {
    display: block;
    width: 100%;
    height: auto;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
    border-radius: 4px;
    z-index: 5;
    position: relative;
    transition: all 5s ease-in-out;
    cursor: pointer; }
    .dress-card img:hover {
      transform: scale(1.2); }
  .dress-card .dress-info {
    position: absolute;
    bottom: 24px;
    left: 16px;
    z-index: 10; }
    .dress-card .dress-info h2 {
      color: white;
      margin-bottom: 0; }

.dress-overlay {
  pointer-events: none; }
  .dress-overlay:after {
    content: '';
    border-radius: 4px;
    position: absolute;
    display: block;
    top: 56px;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 8;
    background: -moz-linear-gradient(bottom, rgba(0, 0, 0, 0.6) 0%, rgba(0, 0, 0, 0) 100%);
    background: -webkit-linear-gradient(bottom, rgba(0, 0, 0, 0.6) 0%, rgba(0, 0, 0, 0) 100%);
    background: linear-gradient(to top, rgba(0, 0, 0, 0.6) 0%, rgba(0, 0, 0, 0) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#a6000000', endColorstr='#00000000',GradientType=0 ); }

.viewing-dress {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: auto; }

.sale-info {
  display: none;
  color: white !important; }
  .sale-info span {
    display: inline-block;
    padding: 0 10px; }
  .sale-info.is-showing {
    display: block; }



