.designer-card {
  width: 100%;
  height: 350px;
  margin-top: 56px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
  border-radius: 4px;
  position: relative; }
  .designer-card .designer-info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: calc(100% + 32px);
    margin-left: -16px;
    background: white;
    position: absolute;
    bottom: 24px;
    border-radius: 3px;
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
    cursor: pointer; }
    .designer-card .designer-info:hover .icon h1 {
      transform: translateX(4px); }
    .designer-card .designer-info h3, .designer-card .designer-info p {
      margin: 0; }
  .designer-card .text {
    margin-left: 32px; }
  .designer-card .icon {
    background: #F5A623;
    height: 100%;
    display: block;
    padding: 10px 20px;
    border-top-right-radius: 3px;
    border-bottom-right-radius: 3px; }
    .designer-card .icon h1 {
      transition: all .15s ease-in-out;
      color: #724802;
      font-size: 26px; }



